<template>
  <div>
    <nav-header></nav-header>
    <nav-bread>
      <span>Goods</span>
    </nav-bread>
    <div class="accessory-result-page accessory-page">
      <div class="container">
        <div class="filter-nav">
          <span class="sortby">Sort by:</span>
          <a href="javascript:void(0)" class="default cur">Default</a>
          <a href="javascript:void(0)" class="price" @click="changeSort">Price
            <svg class="icon icon-arrow-short">
              <use xlink:href="#icon-arrow-short"></use>
            </svg>
          </a>
          <a href="javascript:void(0)" class="filterby stopPop" @click="filterPop">Filter by</a>
        </div>
        <div class="accessory-result">
          <!-- filter -->
          <div class="filter stopPop" id="filter" v-bind:class="{'filterby-show':isPopFilter}">
            <!-- 在这里添加class -->
            <dl class="filter-price">
              <dt>Price:</dt>
              <dd>
                <a href="javascript:void(0)" v-bind:class="{'cur':priceCheck=='all'}" @click="setPriceFilter()">
                  all
                </a>
              </dd>
              <dd v-for="(price,index) in filterPrice" @click="closePopFilter">
                <a href="javascript:void(0)" v-bind:class="{'cur':priceCheck==index}" @click="setPriceFilter(index)">
                  {{price.startPrice}}-{{price.endPrice}}
                </a>
              </dd>
            </dl>
          </div>
  
          <!-- search result accessories list -->
          <div class="accessory-list-wrap">
            <div class="accessory-list col-4">
              <ul>
                <li v-for="item in goodsList">
                  <div class="pic">
                    <a href="#">
                      <img v-lazy="'/static/img/'+item.productImage" alt="item.productImage">
                    </a>
                  </div>
                  <div class="main">
                    <div class="name">{{item.productName}}</div>
                    <div class="price" v-text="item.salePrice"></div>
                    <div class="btn-area">
                      <a href="javascript:;" class="btn btn--m" @click="addCart(item.productId)">加入购物车</a>
                    </div>
                  </div>
                </li>
              </ul>
              <div v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10">
                <img src="./../loading/myloading.svg" v-show="loading">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  
    <div class="md-overlay" v-bind:class="{'overlay-hide':isHideOverlay}" @click="closePopFilter"></div>
    <modal v-bind:showModal="showModal" v-on:close="closeModal">
      <p slot="message">请先登录</p>
      <div slot="btn">
        <a href="javascript:; "class="btn btn--m">关闭</a>
      </div>
    </modal>
    <modal v-bind:showModal="showCartModal" v-on:close="closeModal">
      <p slot="message">加入购物车成功</p>
      <div slot="btn">
        <a class="btn btn--m" @click="showCartModal=false" href="javascript:;"> 继续购物</a>
        <router-link class="btn btn--m" to="/cart" href="javascript:;">查看购物车</router-link>
      </div>
    </modal>
    <nav-footer></nav-footer>
  </div>
</template>
<script>
import '@/assets/css/base.css'
/* import '@/assets/css/product.css' */
import '@/assets/css/login.css'
import NavHeader from './../components/Header.vue'
import NavFooter from './../components/Footer.vue'
import NavBread from './../components/NavBread.vue'
import Modal from './../components/Modal.vue'
import axios from "axios"
import infiniteScroll from 'vue-infinite-scroll'


export default {
  directives: { infiniteScroll },
  data() {
    return {
      goodsList: [],
      filterPrice: [
        { startPrice: '0.00', endPrice: '100.00' },
        { startPrice: '100.00', endPrice: '500.00' },
        { startPrice: '500.00', endPrice: '1000.00' },
        { startPrice: '1000.00', endPrice: '5000.00' }
      ],
      priceCheck: 'all',
      isPopFilter: false,
      isHideOverlay: true,
      page: 1,
      pageSize: 8,
      sortFlag: true,
      busy: true,
      loading:false,
      showModal:false,
      showCartModal:false
    }
  },
  components: {
    NavHeader,
    NavFooter,
    NavBread,
    Modal
  },
  mounted: function () {
    this.getGoodsList()
  },
  methods: {
    getGoodsList(flag) {
      var param = {
        page: this.page,
        pagesize: this.pageSize,
        sort: this.sortFlag ? 1 : -1,
        pricelevel: this.priceCheck
      };
      this.loading = true;
      axios.get("/goods/list", { params: param }).then((result) => {
        this.loading=false
        var res = result.data;
        if (flag) {
          this.goodsList = this.goodsList.concat(res.results)
          if (res.results.length <= 0) {
            this.busy = true
          } else {
            this.busy = false
          }
        } else {
          this.goodsList = res.results
          this.busy = false
        }
        console.log(this.goodsList)
      }).catch(function (err) {
        console.log(err)
      })
    },
    changeSort() {
      this.page = 1;
      this.sortFlag = !this.sortFlag
      this.getGoodsList()
    },
    setPriceFilter(index) {
      if (typeof(index)=='number') {
        this.priceCheck = index;
      }else{
        this.priceCheck = 'all';
      }
      this.page = 1;
      this.getGoodsList()
    },
    filterPop() {
      this.isPopFilter = true;
      this.isHideOverlay = false;
    },
    closePopFilter() {
      this.isPopFilter = false;
      this.isHideOverlay = true;
    },
    addCart(productId){
      axios.post('/goods/addCart',{productId:productId}).then((res)=>{
        if(res.data.status==='0'){
          this.showCartModal = true;
          this.$store.commit("updateCartCount",1)
        }else{this.showModal=true} 
      }).catch((err)=>{
        alert(err)
      })
    },
    loadMore: function () {
      this.busy = true;
      setTimeout(() => {
        this.page++
        this.getGoodsList(true)
      }, 500);
    },
    closeModal(){
      this.showModal=false
      this.showCartModal = false
    }
  }
}
</script>



